<style lang="less">
@bgColor:#072041;
.baseTable_pagenation{
    height: 100%;
 .el-table--enable-row-hover .el-table__body tr:hover>td{
        background-color: #167fc1 !important ;
 }
 .el-table__body tr.current-row>td{
      background-color: #167fc1 !important;
 }
    .el-table{
        margin-top: 16px;
        border-right: 1px solid #025691;
        border-left: 1px solid #025691;
        &::before{
            content: "";
            position: absolute;
            background-color: #025691 !important;
            z-index: 1;
        }


        th.is-leaf{
               border-bottom: 1px solid #025691 !important;
        }
        tr{
            background: transparent ;
        }
         td{
            border-bottom: 1px solid #025691;
        }
         th.is-leaf{
            border-bottom: 1px solid #025691  !important;
        }


        .cell{
            text-align: center;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
        .el-table__header-wrapper{
            .has-gutter{

                color: #a2ecff;
                tr{
                   th{
                       background: #1d477b !important;
                       &>.cell{
                           text-align: center;
                       }
                   }
                }
            }

        }

        .el-table__row{
            height: 56px;
            background: @bgColor !important;
             td{
                 padding: 0px;
                 border-bottom:1px solid #025691;
                .cell{
                    color: #fff;
                    font-size: 14px;
                    text-align: center;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    .el-button{
                        background: none;
                        border: none;
                        color: #00e4ff;
                    }
                }
            }
        }
        /*  */
        .el-table__empty-block{
            background: #025691;
            .el-table__empty-text{
                color: white;
            }

        }

        .green{
            color: rgb(48, 212, 48);
        }
        .gray{
            color: #cccccc;

        }
        .red{
            color:#F56C6C
        }
        .orange{
            color: orangered;
        }
        .yellow{
            color:#e6a23c;
        }
        .cursor{
            cursor: pointer;
            margin-left: 20px;
            text-decoration:underline;
        }
        .specaildeal{
            color: #f7ff19;
        }

    }
    /*  */
    .pagenationBox{
        width: 100%;
        margin-top: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
            .el-pagination__total{
                font-size: 12px;
                color: #bee3fd;
            }
            .el-pagination.is-background .btn-prev{
                display: inline-block;
                width: 47px;
                height: 28px;
                background-color: #146ca9;
                color: #bee3fd;
            }
            .el-pagination.is-background .btn-next{
                display: inline-block;
                width: 47px;
                height: 28px;
                background-color: #146ca9;
                color: #bee3fd;
            }
            .el-pagination.is-background .el-pager li{
               border: solid 1px #146ca9;
               background: #146ca9;
               color: #fff;
            }
            .el-pager li.active{
            border: solid 1px #146ca9;
               background: @bgColor !important;
               color: #fff;
            }

    }
}


</style>
<template>
    <div class="baseTable_pagenation">

        <el-table  
            ref="interfaceTable" 
            highlight-current-row  
            :data="tableData" 
            style="width: 100%"
            @row-click="handleRowClick"
        >
            <el-table-column
                v-for="(arr,index) in tableThead"
                :key="index" 
                :prop="arr.prop"
                :label="arr.name"
                :width="arr.width"
                show-overflow-tooltip
            >
                <template slot-scope="scope">
                    <!--  -->
                    <span v-if="arr.prop == 'state'">
                        <i v-if="scope.row[arr.prop]  == null || scope.row[arr.prop]  == 0"></i>
                        <i v-if="scope.row[arr.prop]== 1 ||scope.row[arr.prop]== 2 ||scope.row[arr.prop]== ''" class="green">
                            <i v-if="scope.row[arr.prop]== 1"></i>
                            <i v-if="scope.row[arr.prop]== 2"></i>
                            <i v-if="scope.row[arr.prop]==''"></i>
                        </i>
                        <i v-if="scope.row[arr.prop]== -1 " class="gray">
                            
                        </i>
                        <i v-if="scope.row[arr.prop]== -2" class="orange">
                           
                        </i>
                        <i v-if="scope.row[arr.prop]== -4" class="yellow">
                           
                        </i>
                        <i v-if="scope.row[arr.prop]== -3" class="yellow">
                            
                        </i>
                        <i v-if="scope.row[arr.prop]== '-'" class="green">
                            -
                        </i>
                        <i v-if="scope.row[arr.prop]== '-'" class="yellow">
                            -
                        </i>
                        <i v-if="scope.row[arr.prop]== ''" class="yellow">
                            
                        </i>

                    </span>
                    <!-- -->
                    <p v-else-if="arr.prop == 'source'">
                        <span v-if="scope.row[arr.prop] == 0"></span>
                        <span v-if="scope.row[arr.prop] == 1"></span>
                        <span v-if="scope.row[arr.prop] == 2"></span>
                        <span v-if="scope.row[arr.prop] == 3"></span>
                    </p>
                    <p v-else-if="arr.prop == 'analog'">
                      {{scope.row[arr.prop1]}}  {{scope.row[arr.prop]}}
                    </p>

                    <!--  -->
                    <el-button
                        @click="getcontent(scope.row,arr.name)"
                        v-else-if="arr.prop == 'faultDetectorNum' || arr.prop == 'alarmNum30Day' || arr.prop == 'highAlarmDetectorNum'"
                    >
                        {{ scope.row[arr.prop] }}
                        <i class="el-icon-search el-icon--right" ></i>
                    </el-button>
                    <!-- --- -->
                    <div v-else-if="arr.prop == 'phaseType'">
                        <ul v-if="scope.row[arr.prop7]">

                            <li v-if="scope.row[arr.prop]== 1">
                                <p>
                                    <span>L{{scope.row[arr.prop1]}}</span>
                                    <span @click="getcontent(scope.row,'L')" class="cursor"></span>
                                </p>

                                <p>
                                    <span>N{{scope.row[arr.prop2]}}</span>
                                    <span @click="getcontent(scope.row,'N')" class="cursor"></span>
                                </p>
                            </li>
                            <li v-if="scope.row[arr.prop]== 2">
                               <p>
                                   <span>L1{{scope.row[arr.prop4]}}</span>
                                   <span @click="getcontent(scope.row,'L1')" class="cursor"></span>
                                </p>
                                <p>
                                   <span>L2{{scope.row[arr.prop5]}}</span>
                                   <span @click="getcontent(scope.row,'L2')" class="cursor"></span>
                                </p>
                                <p>
                                   <span>L3{{scope.row[arr.prop6]}}</span>
                                   <span @click="getcontent(scope.row,'L3')" class="cursor"></span>
                                </p>

                                <p>
                                    <span>N{{scope.row[arr.prop2]}}</span>
                                    <span @click="getcontent(scope.row,'N')" class="cursor"></span>
                                </p>
                            </li>
                        </ul>
                        <ul v-if="scope.row[arr.prop0]">
                            <li>
                                {{scope.row[arr.prop3]}}
                                <span @click="getcontent(scope.row,'A')" class="cursor"></span>
                            </li>
                        </ul>

                    </div>
                    <!--  -->
                    <p v-else-if="arr.prop == 'monitorType'">
                        <span v-if="scope.row[arr.prop]  == 1"></span>
                        <span v-if="scope.row[arr.prop]  == 2"></span>
                    </p>
                    <!-- Ai -->
                    <p v-else-if="arr.prop == 'status'">
                      
                        <span class="yellow" v-if="scope.row[arr.prop]  == -1"></span>
                        <span class="green" v-if="scope.row[arr.prop]  == 1"></span>
                    </p>
                    <p v-else-if="arr.prop == 'currentValue'">
                        <span v-if="scope.row[arr.prop]">{{scope.row[arr.prop]}}</span>
                        <span v-else></span>

                    </p>
                    <!--  -->
                    <div v-else-if="arr.prop == 'coordinateX'">
                           <p>X:{{scope.row[arr.prop]}}</p>
                           <p>Y:{{scope.row[arr.prop1]}}</p>
                    </div>
                    <div v-else-if="arr.prop == 'visionAlarmType'">
                        <span v-if="scope.row[arr.prop] == 1"> </span>
                        <span v-if="scope.row[arr.prop] == 2"> </span>
                        <span v-if="scope.row[arr.prop] == 3"> </span>
                    </div>
                   
                    <!--  -->
                    <div v-else-if="arr.prop == 'alarmHandleStatus'">
                        <el-button  @click="dealDetail(scope.row)" type="text">
                            <span class="specaildeal" v-if="scope.row[arr.prop] == 1"></span>
                            <span v-if="scope.row[arr.prop] == 2"></span>
                            <span v-if="scope.row[arr.prop] == 3"></span>
                            <span v-if="scope.row[arr.prop] == 4"></span>
                        </el-button>
                    </div>
                    <!--  -->
                    <!--  -->
                    <div  v-else-if="!arr.prop">
                        <el-button v-if="specailText" type="text" @click="getDetail(scope.row,scope,specailText)">
                            <span>{{specailText}}</span>
                        </el-button>
                        <div class="table_btns" v-else>
                            <el-button
                                @click="getDetail(scope.row, $event,tableName)"
                                size="mini"
                                type="text"
                                icon="el-icon-search"
                            ></el-button>
                            <el-button
                                v-if="ishownetworkBtn"
                                @click="getnetwork(scope.row, $event)"
                                size="mini"
                                type="text"
                                icon="el-icon-search"
                            ></el-button>
                            <el-button
                                v-if="iselectric"
                                @click="getrefresh(scope.row)"
                                size="mini"
                                type="text"
                                icon="el-icon-refresh-left"
                            ></el-button>
                            <el-button
                                v-if="iselectric"
                                @click="Poweroff(scope.row)"
                                size="mini"
                                type="text"
                                icon="el-icon-warning-outline"
                            ></el-button>
                            <el-button
                                v-if="isMonitor"
                                @click="Monitor(scope.row)"
                                size="mini"
                                type="text"
                                icon="el-icon-warning-outline"
                            ></el-button>
                            <el-button
                            @click="deleteInfo(scope.row)"
                                size="mini"
                                type="text"
                                icon="el-icon-delete"
                            ></el-button>

                        </div>

                    </div>
                     <!--  -->
                    <span style="margin-left: 10px" v-else>{{ scope.row[arr.prop] }} </span>

                </template>
            </el-table-column>

        </el-table>
        <!--  -->
        <div class="pagenationBox">
            <el-pagination
                @current-change="pageChange"
                prev-text=""
                next-text=""
                hide-on-single-page
                :current-page="currentpage"
                :page-size="pageSize"
                background
                layout="total,prev, pager, next"
                :total="totalCount"
            ></el-pagination>
        </div>
    </div>


  <!--  -->

</template>
<script>
import basedelete from '../../httpConfig/commdelete'
export default {
    //
     props: {
     tableName:String,
        tableData:{
            type:Array,
            require: true
        },
        tableThead:{
            type:Array,
            require: true
        },
        specailText:{
            type:String
        },
        totalCount:{//
            type:Number
        },
        pageSize:Number,
        currentpage:{
            type:Number,
            default(){
                return 1
            }
        },
        ishownetworkBtn:{
            type:Boolean,
            default(){
                return false
            }
        },
        iselectric:{
            type:Boolean,
            default(){
                return false
            }
        },
        isMonitor:{
            type:Boolean,
            default(){
                return false
            }
        }
    },
    methods:{
        getcontent(item,propName){
            // console.log(item,propName)
            this.$emit('getdialogContent',item,propName)
        },
        getDetail(index,row,specailText){
           console.log("specailText",specailText)
            if(specailText === ''){
                this.$emit('showDialog',index)
            }else if( specailText === '' || specailText === ''){
                  this.$emit('showdetails',index)
            }else if( specailText === ''){
                this.$emit('showdetails',index,specailText)
            }else if( specailText === ''){
                this.$emit('showdetails',index,specailText)
            }else if( specailText === ''){
                this.$emit('showdetails',index,specailText)
            }else if(specailText === '' || specailText === ''){
                console.log("",index)
                if(specailText === ''){
                    this.$emit('showdetails',row)
                }else{
                     this.$emit('showdetails',index,row)
                }
              
            } else{ //
                this.$emit('showdetails',index)
            }
        },
        getnetwork(item){
            console.log("ssss")
            this.$emit("getnetwork",item)

        },
        //
        getrefresh(item){
            console.log("",item)
            this.$emit("getrefresh",item)
        },
        Poweroff(item){
            console.log("")
            this.$confirm('',{
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(()=>{
              this.$emit("Poweroff",item)
            }).catch(()=>{

            })

        },
        //
        Monitor(item){
             this.$emit("Monitor",item)

        },
        deleteInfo(index){
            console.log("",index)
           this.$confirm('',{
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
           }).then(()=>{
               this.$emit('deleteInfo',index)
           }).catch(()=>{

           })

        },
        pageChange(dataPage){
            console.log("pageChange",dataPage)
            this.$emit('pageChange',dataPage)
        },
        //      
        handleRowClick(val){
            //  console.log("val",val)
        },
        dealDetail(item){
            // console.log(item)
            this.$emit('dealDetail',item)
        }
    },
    watch:{
        tableData:function() {
            this.$nextTick(function() {
                this.$refs.interfaceTable.setCurrentRow(this.tableData[0])
            })
        }
    }
};
</script>





